@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&family=Poppins:wght@300;400;700;900&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

.main {
  min-height: 90vh;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
  position: relative;
  font-family: "Poppins", sans-serif;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  position: relative;
  width: 100%;
}

.main-content-text {
  z-index: 2;
}


.main-content-h1 {
  font-size: 6rem;
  font-weight: 900;
  overflow: hidden;
  z-index: 2;
}

.main-content-btn {
  font-size: 2rem;
  background: #742fc2;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 700;
  box-shadow: 0 0 20px #742fc2;
  cursor: pointer;
}

.dark-h1 {
  color: #494949;
}

.h1-span {
  position: relative;
}

.h1-span::after {
  content: "";
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  border-left: 5px solid #fff;
  background: #1b1b1b;
  margin-left: 115px;
  animation: typing 2.5s steps(13)
    infinite;
  animation-delay: 0.7s;
}

.main-period {
  font-size: 2rem;
  margin-left: 1rem;
  color: #742fc2;
  margin-bottom: 2rem;
}

.main-content-images {
  height: 400px;
  width: 600px;
  z-index: 2;
  position: relative;
  margin-left: 10rem;
  align-self: center;
}

.main-content-images img {
  height: 100px;
  object-fit: contain;
}

.html-logo,
.css-logo,
.js-logo,
.react-logo {
  position: absolute; /* Chrome, Safari 5 */ /* Firefox 5-15 */ /* Opera 12+ */
  animation: move-logo 4s linear
    infinite; /* Chrome, Firefox 16+, IE 10+, Safari 5 */
}

.html-logo {
  top: 0;
  left: 20px;
}

.css-logo {
  top: 50px;
  right: 50px;
}

.js-logo {
  bottom: 50px;
  left: 20px;
}

.react-logo {
  bottom: 0;
  right: 20px;
}

@keyframes move-logo {
  from {
    transform: rotate(0deg)
      translateX(5px) rotate(0deg);
  }
  to {
    transform: rotate(360deg)
      translateX(5px) rotate(-360deg);
  }
}

@keyframes typing {
  100% {
    left: 100%;
  }
}

.stars-container {
  position: absolute;
  height: 100%;
  width: 100%;
}

.main-background-text {
  position: absolute;
  top: 0;
  font-size: 19rem;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #383838;
  color: transparent;
  z-index: 1;
}

.mobile-images {
  display: none;
}

/***** Media Queires *****/
 @media screen and (max-width: 1280px) {
  .main-content-h1 {
    font-size: 5rem;
  }

  .main-content-images {
    width: 350px;
  }

  .main-background-text {
    font-size: 15rem;
  }
} 

@media screen and (max-width: 1024px) {
  .main-content {
    flex-direction: column;
    justify-content: space-evenly;
    overflow: hidden;
  }

  .main-content-text {
    margin: 2rem;
    padding: 0 1rem;
    width: 100%;
  }

  .main-content-h1 {
    font-size: 6rem;
  }

  .mobile-images {
    display: block;
  }

  .desktop-images {
    display: none;
  }

  .main-content-images {
    height: 10vh;
    width: 95%;
    z-index: 2;
    margin:0;
  }

  .main-content-btn {
    padding: 1rem 1rem;
  }

  .main-background-text {
    font-size: 13.5rem;
    top: 20%;
  }
}

@media screen and (max-width: 768px) {
  .main-content-h1 {
    font-size: 4rem;
  }

  .main-background-text {
    display: none;
  }
  .h1-span::after {
    margin-left: 100px;
  }

}

@media screen and (max-width: 540px) {
  .main-content {
    justify-content: space-evenly;
  }

  .main-content-h1 {
    font-size:3rem;
  }

  .main-content-images {
    z-index: 2;
    margin: 0;
  }

  .main-content-images img {
    height: 70px;
  }

  .h1-span::after {
    margin-left: 70px;
  }

  .main-period {
    font-size: 1.2rem;
  }

  .main-content-btn {
    font-size: 1.5rem;
    padding: .5rem;
    align-self: flex-start;
  }
}

@media screen and (max-width: 500px) {
  .main-content-text {
    margin: 0;
  }

  .main-content-h1 {
    font-size: 2.5rem;
  }
  .h1-span::after {
    margin-left: 60px;
    height: 100%;
  }
}

@media screen and (max-width: 400px) {
  .main-content-text {
    margin: 1rem 0;
  }

  .main-content-h1 {
    font-size: 2rem;
  }

  .main-content-images img {
    height: 60px;
  }

  .h1-span::after {
    margin-left: 55px;
  }

}

@media screen and (max-width: 359px) {
  .main-content-h1 {
    font-size: 1.8rem;
  }

  .main-content-images img {
    height: 50px;
  }

  .main-content-btn {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 300px) {
  .main-content-text {
    overflow: hidden;
  }
  .main-content-h1 {
    font-size: 1.5rem;
  }

  .main-period {
    font-size: 1rem;
  }
}
.star {
    position: absolute;
    background-color: transparent;
    box-shadow: 0px 0px 20px #fff;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    opacity: 0; /* Chrome, Safari 5 */  /* Firefox 5-15 */ /* Opera 12+ */
    animation: twinkle infinite ease-in-out; /* Chrome, Firefox 16+, IE 10+, Safari 5 */
}

.star1 {
    top: 30%;
    left: 50%;
    animation-duration: 2s;
}

.star2 {
        top: 20%;
        left: 80%;
        animation-duration: 1.5s;
}

.star3 {
    animation-delay: 1s;
    top: 80%;
    right: 80%;
    animation-duration: 1s;
}

.star4 {
    top: 80%;
    right: 30%;
    animation-duration: 2.5s;
}

.star5 {
    top: 15%;
    right: 90%;
    height: 5px;
    width: 5px;
    animation-duration: 3s;
}

@keyframes twinkle {
    0% {
        opacity: 0;
        transform: scale(0.95);
        background-color: rgba(255,255,255,0.2);
    }

    50% {
        opacity: 1;
        transform: scale(1.1);
        background-color: rgba(255,255,255,1);
    }
    100% {
        opacity: 0;
        transform: scale(0.95);
        background-color: rgba(255,255,255,0.2);
    }
}
