.nav {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: static;
  color: #fff;
  z-index: 4;
  background: #1b1b1b;
  font-family: "Poppins", sans-serif;
}

.nav-logo {
  height: 100px;
  object-fit: contain;
}

.nav-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
}

.nav-link {
  list-style: none;
  padding: .5rem;
  font-size: 1.5rem;
  position: relative;
  cursor: pointer;
}

.nav-link .is-active {
  border-bottom: 5px solid #742fc2;
}

.nav-link a {
  color: #fff;
  text-decoration: none;
}

.burger {
  display: none;
}

.burger div {
  height: 3px;
  width: 25px;
  margin: 5px;
  background: #fff;
  transition: transform 0.2s ease-in-out;
}

.line-1.toggle {
  transform: rotate(-45deg)
    translate(-5px, 6px);
}

.line-2.toggle {
  opacity: 0;
}

.line-3.toggle {
  transform: rotate(45deg)
    translate(-5px, -6px);
}

/***** Media Queires *****/
@media screen and (max-width: 1290px) {
  .nav-links {
    width: 70%;
  }
}

@media screen and (max-width: 770px) {
  .nav {
    position: relative;
    background: #1b1b1b;
  }

  .burger {
    display: inline-block;
  }
  .nav-links {
    position: absolute;
    height: 70vh;
    top: 10vh;
    width: 100%;
    flex-direction: column;
    background: #1b1b1b;
    box-shadow: 0 10px 20px #000;
    transform: scale(0);
    transition: transform 0.2s
      ease-in-out;
  }

  .nav-links.active {
    transform: scale(1);
    transition: transform 0.2s
      ease-in-out;
  }

  .nav-link {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1280px) {
  .nav-logo {
    height: 60px;
  }
}

@media screen and (max-width: 320px) {
  .nav-link {
    font-size: 1.5rem;
  }
}

