@media screen and (max-width: 359px) {
    .react-carousel, .carousel-img {
        width: 300px;
    }
}

@media screen and (max-width: 300px) {
    .react-carousel, .carousel-img {
        width: 250px;
    }
}
.projects {
  background: #1b1b1b;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  overflow: hidden;
  font-family: "Poppins", sans-serif;
}

.projects-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}

.project-titles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  padding: 1rem 0;
}

.project-title {
  font-size: 5rem;
  color: #742fc2;
  z-index: 2;
  font-weight: 900;
}

.project-title-back {
  position: absolute;
  font-size: 7rem;
  color: #242424;
  margin-bottom: 1rem;
}

.projects-h1 {
  width: 50%;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

.projects-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  width: 100%;
}

.project-container {
  border: 5px solid #242424;
  height: 400px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
  z-index: 2;
}

.project-container:hover {
  box-shadow: 0 0 20px #242424;
}

.project-container img {
  height: 350px;
  object-fit: contain;
  border-radius: 10px;
}

.project-container-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  width: 80%;
}

.project-header {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 0 1rem;
  height: 350px;
}

.project-head {
  font-size: 2.5rem;
  color: #742fc2;
  font-weight: 900;
}

.title-technologies {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.title-technology:nth-child(1) {
  margin-left: 0;
}

.title-technology {
  padding: 0.2rem 0.5rem;
  border: 2px solid #742fc2;
  border-radius: 5px;
  font-weight: 700;
  margin: 0 .5rem;
}

.project-description {
  font-size: 1.5rem;
  font-family: "Montserrat", sans-serif;
}

.title-links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.title-link:nth-child(1) {
  margin-left: 0;
}

.title-link {
  margin: 0.5rem;
  position: relative;
  font-weight: 700;
  padding: 0.2rem 0.5rem;
  letter-spacing: 1px;
}

.title-link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  height: 3px;
  width: 100%;
  background: grey;
}

.title-link:hover::after {
  background: #742fc2;
}

.title-links a {
  text-decoration: none;
  color: #fff;
}

/***** media queires *****/

@media screen and (max-width: 1280px) {
  .project-container-content {
    width: 100%;
  }
}

@media screen and (max-width: 1024px) {
  .project-container img {
    height: 280px;
    margin: 0 .5rem;
  }
  .project-title {
    font-size: 4rem;
  }
  
  .project-title-back {
    font-size: 6rem;
  }
}


@media screen and (max-width: 768px) {
    .projects-h1 {
        width: 95%;
      }
    .project-container {
        height: auto;
        padding: .5rem;
    }

    .project-container-content {
        flex-direction: column;
    }

    .left-image {
        flex-direction: column-reverse;
    }

    .project-container img {
        height: 350px;
    }

    .project-header {
        margin: 0;
        height: 300px;
        width: 90%;
    }
}
  
@media screen and (max-width: 540px) {
    .projects-h1, .project-description  {
        font-size: 1.2rem;
      }
    .project-header {
        width: 95%;
    }

    .title-technology {
        font-weight: 400;
    }

    .project-container img {
        height: 250px;
    }

}

@media screen and (max-width: 500px) {
    .project-title-back {
        font-size: 5rem;
    }

    .project-header {
        width: 98%;
    }

    .project-description {
        font-size: 1rem;
    }

    .project-container img {
        height: 195px;
    }

    .title-technology {
        margin: .2rem;
    }
}

@media screen and (max-width: 400px) {
    .project-title-back {
        font-size: 5rem;
    }

    .project-container img {
        height: 170px;
    }
}

@media screen and (max-width: 359px) {
    .project-title {
        font-size: 3rem;
    }
    .project-title-back {
        font-size: 4rem;
    }

    .project-header {
        align-items: center;
        text-align: center;
        height: 350px;
    }

    .title-technologies {
        justify-content: center;
    }
    
    .project-container img {
        height: 145px;
    }

    .project-head {
        font-size: 2rem;
    }

    .title-links {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 300px) {
    .project-container img {
        height: 130px;
    }
}
.footer {
    background: #1b1b1b;
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.footer-socials {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 15%;
    padding: 1rem 0;
    list-style: none;
    color: #000;
}

.footer-socials a {
    color: rgb(199, 199, 199);
    transition: color .2s ease-in-out;
}

.footer-socials a:hover {
    color: #742fc2;
}

.footer-socials i{
    font-size: 2rem;
}

.footer-copyright {
    color: rgb(199, 199, 199);
    font-family: "Montserrat", sans-serif;
}

@media screen and (max-width: 1024px) {
    .footer-socials {
        width: 30%;
    }
}

@media screen and (max-width: 540px) {
    .footer-socials i {
       font-size: 1.5rem;
    }
}

@media screen and (max-width: 500px) {
    .footer-socials {
        width: 60%;
    }
}
